}

@media screen and (min-width: 480px) {
  #leftsidebar {width: 200px; float: left;}
  #main {margin-left: 216px;}
}

body {
  margin: 0;
}

nav {
  display: flex;
  align-items: center;
  flex-direction: row;
  gap: 20px;
  color: purple;
  padding: 7px;

