.nav-img {
  width: 150px;
  margin-right: 5px;
}

.gadgets-div {
  display: block;
  height: 150px;
  width: 100%;
  background: rgb(119, 19, 119);
  border-radius: 20px;
  margin-top: 50px;
  
}

.apple-gadgets {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: black;
  margin-right: auto;
  margin-left: 40px;
}
.mac-pc {
  display: block;
  width: 166px;
  margin-left: auto;
  margin-right: 40px;
}

h2 {
  text-align: center;
  color: purple;
}
.btn-div {
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
  text-align: center;
}

button {
  display: inline;
  margin-left: 30px;
  margin-right: 30px;
  padding: 5px 30px;
  background: white;
  border-radius: 4px;
  border-top: none;
}

button:hover {
  background: purple;
}

.shopping-item {
  width:150px;
}

.shopping-item-img {
  width: 150px;
}
.shopping-menu {
  display: grid;
  grid-template-columns: auto auto auto auto;
  margin-left: 30px;
}
